body{
    font-family: Roboto;
}

/* roboto-regular - latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/roboto-v30-latin-regular.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
  }

.above{
    background: conic-gradient(from 0.2525turn at 0% 85%, #423769, 100deg, #382D61);
    display: grid;
    place-items: center;
    text-align: center;
    padding: 1rem;
    padding-bottom: 2.5rem;
}

.above h1{
    margin: 0;
    padding: 0;
}

.above h2{
    padding: 0;
    margin: 0;
}

.above p{
    display: flex;
    flex-grow: 1;
    place-items: center;
    width: 25%;
    text-align: left;
    padding: 0.5rem;
    margin: 0;
}

.first{
    background-color: #261C49;
    padding: 4rem 1.5rem 2rem 6rem;
    display: flex;
    align-items: center;
    background: conic-gradient(from -0.258turn at 100% 85%, #261C49, 100deg, #28195E);
}

.first h1{
    margin: 0;
}

.first .text{
    display: block;
    flex-grow: 0.25;

}

.first .imgcontainer{
    
    flex-grow: 1;
    display: flexbox;
    align-items: center;

}

.first .imgcontainer img{
    position: relative;
    float: left;
    border-radius: 20px;
    margin: 0.25rem;
}


.second{
    padding: 3rem;
    background-color: #140F28;
    background: linear-gradient(#140F28 80%, #140f2865);
    display: flex;
    align-items: center;
    justify-content: center;
}

.second .text{
    display: block;
}

.second h1{
    margin: 0;
    padding: 0;
}


.TechStack{
    background-color: #120f20;
    display: flex;
    justify-content: center;
    padding: 5rem;
    padding-left: 0;
    height: 100vh;
}

.TechStack .list{
    width: 50%;
    height: fit-content;
}

.TechStack .list .inlinelist button{
    border-radius: 10px;
    font-size: 125%;
    padding: 0 1rem;
    width: 275px;
    height: 80px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    flex-grow: 1;
    margin: 0.15rem;
    border-style: none;
}

.TechStack .list .inlinelist button img{
    display: inline-block;
    position: relative;
}

.TechStack .list .inlinelist button p{
    display: inline-block;
    font-size: 120%;
    font-weight: 600;
    margin-left: 0.5rem;
}

.TechStack .text{
    flex-grow: 1;
    display: flex;
    align-items: start;
    margin-top: 4rem;
    margin-left: 5%;
}

.TechStack .list .inlinelist .left1{
    width: 40px;
    background-color: rgb(131, 131, 131);
    border-radius: 0 10px 10px 0;
    margin: 0.15rem;
    filter: blur(2px);
}

.TechStack .list .inlinelist .left3{
    width: 10px;
    background-color: rgb(131, 131, 131);
    border-radius: 0 10px 10px 0;
    margin: 0.15rem;
    filter: blur(2px);
}

.TechStack .list .inlinelist .left4{
    width: 120px;
    background-color: rgb(131, 131, 131);
    border-radius: 0 10px 10px 0;
    margin: 0.15rem;
    filter: blur(2px);
}

.TechStack .list .inlinelist .left5{
    width: 55px;
    background-color: rgb(131, 131, 131);
    border-radius: 0 10px 10px 0;
    margin: 0.15rem;
    filter: blur(2px);
}

.TechStack .list .inlinelist .left6{
    width: 70px;
    background-color: rgb(131, 131, 131);
    border-radius: 0 10px 10px 0;
    margin: 0.15rem;
    filter: blur(2px);
}

.TechStack .list .inlinelist .unused{
    background-color: rgb(131, 131, 131);
    filter: blur(2px);
    width: 275px;
}

.TechStack .text .bigger{
    font-size: 130px;
    padding-top: 0;
    display: flex;
    margin: 0;
    line-height: 0.9;
}

.TechStack .text p{
    display: flex;
    color: #FFCCA3;
    padding: 0;
    margin: 0;
    border: none;
}

.inlinelist:nth-child(1){
    margin-right: 1rem;
}

.inlinelist:nth-child(2){
    margin-right: 4rem;
}

.inlinelist:nth-child(3){
    margin-right: 3rem;
}

.inlinelist:nth-child(4){
    margin-right: 5.5rem;
}

.inlinelist:nth-child(6){
    margin-right: 7rem;
}

.inlinelist{
    display: flex;
    width: max-content;
}